import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppRoutingModule} from "./app-routing.module";
import { FormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { RegistrationComponent } from './registrations/registration/registration.component';

import { NurseRegistrationComponent } from './registrations/nurse-registration/nurse-registration.component';
import { FacilityRegistrationComponent } from './registrations/facility-registration/facility-registration.component';
import { UserComponent } from './user/user.component';
import { FacilityDashboardComponent } from './dashboards/facility-dashboard/facility-dashboard.component';
import { NurseDashboardComponent } from './dashboards/nurse-dashboard/nurse-dashboard.component';
import { NurseProfileComponent } from './dashboards/nurse-profile/nurse-profile.component';
import { FacilityProfileComponent } from './dashboards/facility-profile/facility-profile.component';
import { NurseAssignmentLogComponent } from './dashboards/nurse-assignment-log/nurse-assignment-log.component';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    RegistrationComponent,
    NurseRegistrationComponent,
    FacilityRegistrationComponent,
    UserComponent,
    FacilityDashboardComponent,
    NurseDashboardComponent,
    NurseProfileComponent,
    FacilityProfileComponent,
    NurseAssignmentLogComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



